.tarea-container {
    width: 500px;
    min-height: 65px;
    background-color: #1b1b32;
    margin: 5px 0;
    padding: 8px 15px 8px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 25px;
    border: 1px solid whitesmoke;
    cursor: pointer;
}

.tarea-container:nth-child(3n + 1) {
    background-color: #1b1b32;
}

.tarea-container:nth-child(3n + 2) {
    background-color: #2a2a40;
}

.tarea-container:nth-child(3n + 3) {
    background-color: #3b3b4f;
}


/* overflow-wrap: anwyhere  es como el alt z en el vsc */
.tarea-texto {
    width: 100%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    overflow-wrap: anywhere;
}

.tarea-icono {
    width: 25px;
    height: 25px;
    margin: 5px;
}

.tarea-contenedor-iconos {

}

.tarea-container.completada {
    background-color: #5a01a7;
    text-decoration: line-through;
}

